.team {
  &__container {
    padding: 44px 0 140px;

    h2 {
      margin: 0 0 48px;
      font-size: 54px;
      line-height: 65px;
    }
  }

  // &__swiper {
  //   position: relative;

  //   button {
  //     position: absolute;
  //     top: 0;
  //     bottom: 0;
  //   }
  // }

  &__prev-btn {
    left: -65px;
  }

  &__next-btn {
    right: -65px;
  }

  &__list {
    display: flex;
    gap: 40px;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 260px;
    height: 294px;
    padding: 30px 55px;

    color: $color-default-white;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
    }

    h3 {
      margin: 0;
      margin-top: auto;
      text-align: center;

      font-size: 18px;
      line-height: 27px;
    }

    &:hover {
      background-color: $color-sappire-blur;

      h3 {
        margin: 0;
      }

      .team__desc {
        display: block;
      }
    }
  }

  &__desc {
    display: none;

    p {
      margin: 0;
      margin-bottom: 45px;
      color: $color-spindle;
      text-align: center;
    }
  }

  &__skills {
    display: flex;
    flex-direction: column;
    gap: 24px;

    padding: 0;
    font-size: 14px;
    line-height: 21px;
    list-style-type: disc;
  }
}
