.about {
  &__container {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 660px;
    gap: 85px;

    padding: 120px 0 235px;

    img {
      min-width: 100%;
    }
  }

  &__wrapper {
    padding-left: 60px;
    display: flex;
    flex-direction: column;

    h2 {
      margin: 37px 0 7px;
      font-size: 48px;
      line-height: 58px;
    }

    p {
      font-weight: 300;
      margin-top: 8px;
      margin-bottom: 1px;
    }
  }

  &__desc {
    margin-bottom: 30px;

    font-size: 18px;
    line-height: 27px;
  }

  &__video {
    position: absolute;
    bottom: 120px;
    left: 437px;

    width: 364px;
    height: 228px;

  }
}
